.header {
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    position: sticky;
    background-color: white;
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.headerLeft {
    display: flex;
    justify-content: space-evenly;
}

.headerInput {
    display: flex;
    align-items: center;
    background-color: #eff2f5;
    padding: 10px;
    margin-left: 10px;
    border-radius: 999px;
}

.headerLeft > img {
    height: 50px;
}

.headerInput > input {
    border: none;
    background-color: transparent;
    outline-width: 0;
}

.headerMid {
    display: flex;
    flex: 1;
    justify-content: center;
}

.headerOpt > .MuiSvgIcon-root {
    color: gray;
}

.headerOpt:hover > .MuiSvgIcon-root {
    color: #c92ef4;
}

.headerOpt--active > .MuiSvgIcon-root {
    color: #c92ef4;
}

.headerOpt--active {
    border-bottom: 4px solid #c92ef4;
}

.headerOpt {
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
}

.headerOpt:hover {
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 0 30px;
    border-bottom: none;
}

.headerInfo {
    display: flex;
    align-items: center;
}

.headerInfo > h4 {
    margin-left: 10px;
    margin-right: 10px;
}

.headerRigt {
    display: flex;
}